import React, { useEffect, useState, Suspense, lazy} from 'react'
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";

// Components Imports
//import SideBar from "";

//Pages Imports
// import Dashboard from "./Dashboard";
// import Readers from '';
// import AdjustmentMaker from "../components/Adjustment/AdjustmentMaker";
// import AdjustmentChecker from "../components/Adjustment/AdjustmentChecker";
// import ForceMatchMaker from "";
// import ForceMatchChecker from "";
// import Header from '';
// import Loader from '';

const Dashboard = lazy(() => import('./Dashboard'));
const SideBar = lazy(() => import('../components/Navbar/Sidebar'));
const Readers = lazy(() => import('../components/Readers/Readers'));
const AdjustmentMaker = lazy(() => import('../components/Adjustment/AdjustmentMaker'));
const AdjustmentChecker = lazy(() => import('../components/Adjustment/AdjustmentChecker'));
const ForceMatchMaker = lazy(() => import('../components/ForceMatch/ForceMatchMaker'));
const ForceMatchChecker = lazy(() => import('../components/ForceMatch/ForceMatchChecker'));
const Header = lazy(() => import('../components/Navbar/Header'));
const Loader = lazy(() => import('../components/Other/Loader'));


function Routing() {
  const [isLoading, setIsLoading] = useState(true);
  useEffect(() => {
    setTimeout(() => {
      setIsLoading(false);
    }, 3000);
  });
  return (

      <>
        {isLoading ? (<Loader />) : 
          <Suspense fallback={<Loader/>}>
              <Router>
                <SideBar>
                  <Header></Header>
                  <Routes>
                    <Route path="/" element={<Dashboard />} />
                    <Route path="/Readers/Readers" element={<Readers />} />
                    <Route path="/Adjustment/AdjustmentMaker" element={<AdjustmentMaker />} />
                    <Route path="/Adjustment/AdjustmentChecker" element={<AdjustmentChecker />} />
                    <Route path="/ForceMatch/ForceMatchMaker" element={<ForceMatchMaker />} />
                    <Route path="/ForceMatch/ForceMatchChecker" element={<ForceMatchChecker />} />
                    <Route path="*" element={<> not found</>} />
                  </Routes>
                </SideBar>
            </Router>
          </Suspense>
        }
      </>
      

    // <Router>
    //     <SideBar>
    //       <Header></Header>
    //         <Routes>
    //             <Route path="/" element={<Dashboard />} />
    //             <Route path="/Readers/Readers" element={<Readers />} />
    //             <Route path="/Adjustment/AdjustmentMaker" element={<AdjustmentMaker />} />
    //             <Route path="/Adjustment/AdjustmentChecker" element={<AdjustmentChecker />} />
    //             <Route path="/ForceMatch/ForceMatchMaker" element={<ForceMatchMaker />} />
    //             <Route path="/ForceMatch/ForceMatchChecker" element={<ForceMatchChecker />} />
    //             <Route path="*" element={<> not found</>} />
    //         </Routes>
    //     </SideBar>
    // </Router>
  )
}

export default Routing